*, *:before, *:after {
  box-sizing: border-box;
}
html{
    overflow-x:hidden;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  color: #333;
  background-color: #eee;
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x:hidden;
}

img{
    vertical-align:middle;
}

a{
    color:#222;
    text-decoration:none;
}

.clearfix {
  overflow: auto;
  zoom: 1;
}

#content{
    width: 100%;
    max-width:768px;
    margin: 0 auto;
}

.top{
    background-color: #eee;
    .logo{
        width: 100%;
    }
    
}


.layout-img{
    width: 100%;
}

header{

    .nav{
        margin: 0;
        padding: 0;
        width: 100%;
        li{
            width: 16%;
            background-color: #168648;
            display:inline-block;
            text-align:center;
            border-left:1px solid #fff;
            &:first-child{
                width: 20%;
                border-left:none;
            }
            img{
                width: 20%;
                display:inline-block;
            }
            a{
            text-decoration: none;
            }
            p{
                padding-left:.2rem;
                display:inline-block;
                color:#eee;
                font-size:.8rem;
                line-height: 1.2rem;
                text-align:center;
                margin: .2rem 0;
            }
        }
    }
    
}


.section{
    width: 100%;
    background-color: #fff;
    margin-top: 1rem;
    .section-title{
        margin: .5rem;
        margin-bottom: 0;
        padding: .5rem;
        padding-left:5%;
        padding-bottom: 0;
        font-family: 微软雅黑, Verdana, Geneva, sans-serif,宋体;
        font-size: 1.2rem;
        color: #575757;
        text-align:left;
    }
}

.welware{
    text-align:center;
    img{
        width: 90%;
        margin-bottom: 1em;
    }
}

.news{
    ul{
        margin-top: 0;
        padding-left:5%;
        width: 95%;
        .new-list{
            list-style:none;
            padding-bottom: 1rem;
            img{
                width: 30%;
                float:left;
                margin-top: .8rem;
            }
            .new-content{
                padding-left:32%;
                h3{
                    font-size:.8rem;
                    line-height: 1em;
                }
                p{
                    font-size:.8rem;
                }
            }
        }
    }
}

/* Product */
.product {
    width: 100%;
    img{
        width: 100%;
    }
}
.sub-section{
    padding:5%;
    ul{
        list-style:none;
        margin: 0;
        padding: 0;
        li{
            border-bottom: 1px solid #aaa;
            &:last-child{
                border-bottom:none;
            }
        }
    }
    h3{
        color: #168648;
        font-size: 1.1rem;
    }

    p{
        color: #575757;
        font-size:1rem;
        text-indent:2rem;
    }
    img{
        width: 100%;
    }
    img.special-img{
        margin-bottom: 1rem;
    }
}

.order-title{
    width: 100%;
    text-align:center;
    margin-bottom: 1rem;
    img{
        width: 100%;
    }
}

em{
    color:red;
    font-style:none;
}
.orderForm{
    padding:5%;
    color:#575757;
    legend{
        color:#575757!important;
    }
    .order-submit-wrapper{
        text-align:center;
        .order-submit{
                background: #168648;
                padding: 5px 40px;
                margin: 1rem auto;
                color: #fff;
                font-size: 18px;
                font-family: 微软雅黑;
        }
    }

    .form-textarea{
        width: 100%;
    }
}

.widget{
    position:fixed;
    right: 0;
    top:20rem;
    z-index:9999;
    img{
        display:block;
        width: 3rem;
    }
}

.brand{
    img{
        width: 100%;
    }
}


footer{
    width: 90%;
    margin-left: 5%;
    font-size:.6em;
    color:#fff;
}

